@font-face {
    font-family: 'LapsusPro';
    font-weight: bold;
    src: url("./../assets/fonts/LapsusPro-Bold.otf") format("opentype");
}

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
}

body {
    background-color: #74afd5;
    background-image: url("./../assets/images/background_clouds.png"), url("./../assets/images/rainbow.png");
    background-repeat: repeat, no-repeat;
    background-size: 60%, 30%;
    background-position: center, bottom -100px right -300px;
}

.navbar {
    background-color: #9b60cd;
    box-shadow: 0 1px 2px rgba(0,0,0,0.4);
}

.navbar-nav .nav-link {
    font-family: 'LapsusPro', cursive;
    padding: 1rem 2rem;
    font-size: 36px;
    color: #FFF;
    letter-spacing: 4px;
    text-shadow: 0px 1px 3px rgba(0,0,0,0.7);
    transition: all 0.2s linear;
}

.navbar-nav .nav-link:hover{
    -webkit-transform: scale(1.2);
    text-shadow: 0px 0px 50px rgba(255,255,255,0.7), 0px 1px 3px rgba(0,0,0,0.7);
}

@media (min-width: 992px) {
    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: 2rem;
        padding-left: 2rem;
    }
}

.examples {
    padding-top: 180px;
}

.examples .col {
    padding-left: 8px;
    padding-right: 8px;
}

.examples .col img {
    box-shadow: 0 0 2px rgba(0,0,0,1), 0 1px 3px rgba(0,0,0,0.4);
}

.description {
    font-family: 'LapsusPro', cursive;
    font-size: 26px;
    color: #FFF;
    letter-spacing: 4px;
    text-shadow: 0px 1px 3px rgba(0,0,0,0.7);
    padding-bottom: 180px;
}

.description .btn-primary {
    font-family: 'LapsusPro', cursive;
    font-size: 26px;
    color: #FFF;
    letter-spacing: 4px;
    text-shadow: 0px 1px 3px rgba(0,0,0,0.7);
    background-color: #967ff8;
    border-color: #967ff8;
    padding: 0.5rem 3rem;
    box-shadow:  0 0 2px rgba(0,0,0,1), 0 1px 3px rgba(0,0,0,0.4);
    transition: all 0.2s linear;
}

.description .btn-primary:hover {
    -webkit-transform: scale(1.2);
    text-shadow: 0px 0px 50px rgba(255,255,255,0.7), 0px 1px 3px rgba(0,0,0,0.7);
}
